@import '../styles/sass/mixins';


.paginatorContainer {
border: 1px solid black;
  padding: 2px;
  margin: 3px;
  width: 100%;
  @include flex(row, left, center, wrap);


  .selectedPage {
    font-weight: bold;
    color: black;
  }

  .nonSelectedPage {
    color: dimgrey;
    margin: 3px;
  }

}